import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import ButtonGroup from "@mui/material/ButtonGroup";
import { Grid } from "@mui/material";
import "./cardButton.scss";
import { buttonStyles } from "./ButtonStyles";
import { useSelector } from "react-redux";
import Constants from "../../constants";
import { Link } from "react-router-dom";

export const CardButton = () => {
  const cart = useSelector((state) => state.cart);
  const [goodsQuantity, setGoodsQuantity] = useState(0);
  const [goodsSum, setGoodsSum] = useState(0);
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const columns = [
    { id: "num", label: "№", minWidth: 30 },
    { id: "good", label: "Товар", minWidth: 170 },
    { id: "price", label: "Цена", minWidth: 100 },
    {
      id: "quantity",
      label: "Количества",
      minWidth: 170,
      align: "center",
    },
    {
      id: "total",
      label: "Общая",
      minWidth: 170,
      align: "right",
    },
  ];

  return (
    <>
    <Link to='/basket'>
        <Button
          sx={buttonStyles}
          onClick={handleOpen}
          variant="outlined"
          className="cartButton"
          startIcon={<ShoppingCartIcon className="shop-icon" />}
        >
          {`${cart.cartItems.length}`} товаров - {`${goodsSum}`} тг.
        </Button>
      </Link>  
    </>
  );
};
