import React from 'react';
import {
  TableContainer,
  Table,
  TableHead,
  TableCell,
  TableBody,
  TableRow,
  Button,
} from "@mui/material";
import {Typography, Breadcrumbs} from "@mui/material";
import Box from "@mui/material/Box";
import { useSelector } from "react-redux";
import { Grid } from "@mui/material";
import Constants from "../../common/constants";
import { ContentHeader } from '../../common/Content/ContentHeader/ContentHeader';
import { ContentScheme } from '../../common/helpers/contentScheme/ContentScheme';
import { HeadMenu } from '../../common/helpers/HeadMenu/HeadMenu';
import Adverts from '../Adverts';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import './basket.scss'

export const Basket = () => {
  const navigate = useNavigate();
  const cart = useSelector((state) => state.cart);
  
  const columns = [
    { id: "num", label: "№", minWidth: 30 },
    { id: "good", label: "Товар", minWidth: 170 },
    { id: "price", label: "Цена", minWidth: 100 },
    {
      id: "quantity",
      label: "Количества",
      minWidth: 170,
      align: "center",
    },
    {
      id: "total",
      label: "Общая",
      minWidth: 170,
      align: "right",
    },
  ];
  return (
    <>
        <ContentHeader />
        <HeadMenu />
        <ContentScheme sidebar={Adverts()} >
        <h1>Корзинка</h1>
        <Breadcrumbs className='breadcrumbs' aria-label="breadcrumb" sx={{color:'rgba(0,0,0,.20)'}}>
          <Link underline="hover" color="inherit" to="/">
          Главная
          </Link>
          <Link underline="hover" color="inherit" to="/catalog">
          Каталог
          </Link>
          <Typography color="text.primary">Корзинка</Typography>
        </Breadcrumbs>
       <Box className="">
          <Grid>
            <div className="cart-items">
              {cart.cartItems.length === 0 ? (
                <div className="noBasket">
                <Typography
                  sx={{
                    textAlign: "center",
                    marginTop: "20px",
                    fontSize: "20px",
                  }}
                >
                  Ваша корзина пусто
                </Typography>
                <Button sx={{margin:'20'}} onClick={() => navigate(-1)} startIcon={<ArrowBackIcon />}>
                  Вернуться назад
                </Button>
                </div>
              ) : (
                <div className="basket">
                  {cart.cartItems?.map((cartItem, i) => (
                    <div className="cart-item">
                      <div className="cart-num">
                        {++i}
                      </div>
                      <div className="cart-info">
                        <div className="img"><img src={Constants.API_URL + cartItem.img} alt="" /></div>
                        <div className="desc">
                          <h4>{cartItem.title}</h4>
                          <p>{cartItem.short_desc}</p>
                          <h5>Фирма: -</h5>
                        </div>
                        <div className="actions">
                          <p className='price'>{cartItem.price} KZT/шт</p>
                          
                        </div>
                        
                      </div>
                    </div>
                  ))}
                <div className="cart-info-bottom">
                <Grid className="left">
                  Общий количество товаров:
                  <span> {cart.cartTotalQuantity}</span>
                </Grid>
                <Grid className="right">
                  <p>Сумма: 3 200 КZT</p>
                  <p>Cкидка: -320 КZT (10%)</p>
                  <p>Итого: 2 880 КZT</p>
                </Grid>
              </div>
              <div className="btn-order">
                <button>Оформить заказ</button>
              </div>
              </div>
              )}
              
            </div>
          </Grid>
        </Box>
        </ContentScheme>
    </>
  )
}